<template>
    <div>
        <van-nav-bar
                title="每天一个好心情"
                left-text="返回"
                left-arrow
                @click-left="onClickLeft"
                class="clock_in"
        />
        <div style="margin-top: 46px">
            <p class="mine_article">我的文章</p>
            <van-cell-group v-for="(item,index) in content" :key="index">
                <router-link :to="{path:'/details_article',query:{id:item.news_id,router:'/person_article'}}">
                    <van-cell :title="item.news_title" class="cell" is-link/>
                </router-link>
            </van-cell-group>
        </div>

    </div>
</template>

<script>
    export default {
        name: "personarticle",
        data(){
            return{
                content:[],
            }
        },
        methods:{
            onClickLeft(){
                this.$router.push({
                    path:'/',
                    query:{
                        active:2
                    }
                })
            },
        },
        mounted() {
            this.$axios.get('https://zhuzuoji.com/myapi/blog/findmyblog',{
                params:{
                    key:'vv9544',
                    email:this.$getCookie(' email')
                }
            }).then(res=>{
                console.log(res)
                if(res.status==200&&res.data.state){
                    this.content=res.data.moments
                }else {
                    this.$toast.fail('查询失败')
                }
            }).catch(err=>{
                let error=err.toString()
                if(error.indexOf('Network')!=-1){
                    this.$toast.fail('请检查网络')
                }
            })
        }
    }
</script>

<style scoped>
    .clock_in {
        background-color: rgb(146,188,255);
        position: fixed;
        width: 100%;
        height: 46px;
        top: 0;
    }
    .van-cell{
        padding: 5px 16px;
        border-radius: 5px;
        margin: 5px auto;
        width: 300px;
        border: 1px solid #ccc;
    }
    .mine_article{
        text-align: center;
    }
    .van-hairline--top-bottom::after{
        border-width: 0px;
    }
    >>>.van-nav-bar__text{
        color: #fff;
    }
    >>>.van-icon::before{
        color: #fff;
    }
    >>>.van-nav-bar__title{
        color: #fff;
    }
    .van-cell--clickable{
        text-align: left;
    }
</style>